<template>
  <v-card>
    <v-card-text>
      <h3>Dynamic Viscosity</h3>
      <convert-box></convert-box>
    </v-card-text>
  </v-card>
</template>

<script>
import ConvertBox from '../components/ConvertBox'

export default {
  components: { ConvertBox },
  data() {
    return {
      unit: [
        {
          text: 'Centipoise (cP)',
          val: 1
        },
        {
          text: 'Gram per centimeter second (g/(cms))',
          val: 0.01
        },
        {
          text: 'Kilogram per metre second (kg/(ms))',
          val: 0.001
        },
        {
          text: 'Newton second per square metre (Ns/m)',
          val: 0.001
        },
        {
          text: 'Pascal-second (Pas)',
          val: 0.001
        },
        {
          text: 'Poise (P)',
          val: 0.01
        },
        {
          text: 'Pound-force second per square inch (lbfs/in)',
          val: 1.45037737797e-7
        },
        {
          text: 'Pound-force second per square foot (lbfs/ft)',
          val: 0.0000208854342332
        },
        {
          text: 'Pound per foot second (lb/(fts))',
          val: 0.00067196897514
        },
        {
          text: 'Pound per foot hour (lb/(fth))',
          val: 2.419088310502
        },
        {
          text: 'Slug per foot second (slug/(fts))',
          val: 0.0000208854342332
        },
        {
          text: 'Poundal second per square foot',
          val: 0.0001019716212978
        },
        {
          text: 'Dyne second per square centimetre (dyns/cm)',
          val: 0.01
        }
      ]
    }
  },
  created() {
    this.$store.dispatch('setUnit', this.unit)
  }
}
</script>

<style>
</style>



