<template>
  <v-card>
    <v-card-text>
      <h3>Electrical Resistance</h3>
      <convert-box></convert-box>
    </v-card-text>
  </v-card>
</template>

<script>
import ConvertBox from '../components/ConvertBox'

export default {
  components: { ConvertBox },
  data() {
    return {
      unit: [
        {
          text: 'Abohm (ab)',
          val: 1
        },
        {
          text: 'Gigaohm (G)',
          val: 1e-18
        },
        {
          text: 'Kiloohm (k)',
          val: 1e-12
        },
        {
          text: 'Megaohm (M)',
          val: 1e-15
        },
        {
          text: 'Microohm ()',
          val: 0.001
        },
        {
          text: 'Milliohm (m)',
          val: 0.000001
        },
        {
          text: 'Nanoohm (n)',
          val: 1
        },
        {
          text: 'Ohm ()',
          val: 1e-9
        },
        {
          text: 'Volt per ampere (V/A)',
          val: 1e-9
        }
      ]
    }
  },
  created() {
    this.$store.dispatch('setUnit', this.unit)
  }
}
</script>

<style>
</style>



